@tailwind base;
@tailwind components;
@tailwind utilities;

/* F1 Racing Theme CSS */
.App {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

/* Racing-style animations */
@keyframes pulse-race {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

@keyframes slide-in {
  from { transform: translateX(-100%); opacity: 0; }
  to { transform: translateX(0); opacity: 1; }
}

@keyframes fade-in {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Custom racing components */
.racing-grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.leaderboard-item {
  animation: slide-in 0.3s ease-out;
  transition: all 0.2s ease;
}

.leaderboard-item:hover {
  transform: translateX(4px);
  background-color: rgba(239, 68, 68, 0.1);
}

.race-event {
  animation: fade-in 0.5s ease-out;
}

/* Position badges */
.position-1 { @apply bg-yellow-500 text-black; }
.position-2 { @apply bg-gray-400 text-black; }
.position-3 { @apply bg-orange-600 text-white; }
.position-default { @apply bg-gray-600 text-white; }

/* Team colors */
.team-redbull { @apply border-l-4 border-blue-600; }
.team-ferrari { @apply border-l-4 border-red-600; }
.team-mclaren { @apply border-l-4 border-orange-500; }
.team-mercedes { @apply border-l-4 border-cyan-400; }
.team-astonmartin { @apply border-l-4 border-green-500; }
.team-alpine { @apply border-l-4 border-pink-500; }
.team-williams { @apply border-l-4 border-blue-400; }
.team-alphatauri { @apply border-l-4 border-indigo-500; }
.team-alfaromeo { @apply border-l-4 border-red-500; }
.team-haas { @apply border-l-4 border-gray-500; }

/* Racing telemetry charts */
.telemetry-chart {
  background: linear-gradient(135deg, rgba(15, 23, 42, 0.8) 0%, rgba(30, 41, 59, 0.8) 100%);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 8px;
}

/* Live status indicators */
.status-live {
  animation: pulse-race 2s infinite;
  @apply bg-red-500;
}

.status-stopped {
  @apply bg-gray-500;
}

.status-connecting {
  animation: pulse-race 1s infinite;
  @apply bg-yellow-500;
}

/* Racing HUD elements */
.racing-hud {
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-weight: 600;
  letter-spacing: 0.05em;
}

/* Track visualization */
.track-minimap {
  background: radial-gradient(circle at center, #1f2937 0%, #111827 100%);
  border: 2px solid #374151;
}

.car-dot {
  transition: all 0.3s ease;
  filter: drop-shadow(0 0 4px rgba(239, 68, 68, 0.8));
}

/* Event feed styling */
.event-feed {
  max-height: 400px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #4b5563 #1f2937;
}

.event-feed::-webkit-scrollbar {
  width: 6px;
}

.event-feed::-webkit-scrollbar-track {
  background: #1f2937;
}

.event-feed::-webkit-scrollbar-thumb {
  background: #4b5563;
  border-radius: 3px;
}

/* Responsive design */
@media (max-width: 768px) {
  .racing-grid {
    grid-template-columns: 1fr;
  }
  
  .leaderboard-item {
    font-size: 0.875rem;
  }
}

/* Button styles */
.btn-race-start {
  @apply bg-green-600 hover:bg-green-700 text-white font-bold py-3 px-6 rounded-lg transition-all duration-200 transform hover:scale-105;
}

.btn-race-stop {
  @apply bg-red-600 hover:bg-red-700 text-white font-bold py-3 px-6 rounded-lg transition-all duration-200 transform hover:scale-105;
}

.btn-disabled {
  @apply bg-gray-600 text-gray-400 cursor-not-allowed;
}

/* Data cards */
.data-card {
  @apply bg-gray-800 border border-gray-700 rounded-lg p-4 shadow-lg;
  transition: all 0.3s ease;
}

.data-card:hover {
  @apply border-gray-600 shadow-xl;
  transform: translateY(-2px);
}